@font-face {
  font-family: "Calibre Regular";
  src: url("../fonts/calibre/regular/Calibre-Regular.ttf") format("truetype"), 
  	url("../fonts/calibre/regular/Calibre-Regular.woff") format("woff"), 
  	url("../fonts/calibre/regular/Calibre-Regular.svg") format("svg"), 
  	url("../fonts/calibre/regular/Calibre-Regular.eot");
}
  
@font-face {
  font-family: "Calibre Medium";
  src: url("../fonts/calibre/medium/Calibre-Medium.ttf") format("truetype"), 
  	url("../fonts/calibre/medium/Calibre-Medium.svg") format("svg"), 
  	url("../fonts/calibre/medium/Calibre-Medium.woff") format("woff"), 
  	url("../fonts/calibre/medium/Calibre-Medium.eot");
}

@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon/icomoon.eot?pcckke');
  src:  url('../fonts/icomoon/icomoon.eot?pcckke#iefix') format('embedded-opentype'),
    url('../fonts/icomoon/icomoon.ttf?pcckke') format('truetype'),
    url('../fonts/icomoon/icomoon.woff?pcckke') format('woff'),
    url('../fonts/icomoon/icomoon.svg?pcckke#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-dribbble2:before {
  content: "\e903";
}
.icon-envelope-o:before {
  content: "\e904";
}
.icon-linkedin:before {
  content: "\e900";
}
.icon-instagram:before {
  content: "\e901";
}
.icon-github:before {
  content: "\e902";
}

